.table-row {
  background-color: white;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  padding: 15px;
  text-transform: capitalize;
  font-weight: bold;
  text-align: center;
  border-bottom: 1px solid $bgcColor;

  &:hover {
    outline: 2px solid $mainColor;
  }

  .bold {
    color: $secondaryColor;
  }
}
